/*************************
GENERAL STYLING
**************************/
.avatar--main {
  width: 100%;
  margin-top: 4.62em;
  background-color: #f7f7f7;
  padding: 2em 1.5em;
  font-family: "Poppins", sans-serif;
}
.avatar--main section {
  max-width: 720px;
  margin: 0 auto;
}
.avatar--main h2 {
  font-family: "Nunito", sans-serif;
  margin: 1em 0;
  letter-spacing: 0.03em;
  font-size: 1.6em;
  color: var(--h1-color);
  font-weight: 700;
  position: relative;
}
.avatar--main h2:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 3.5em;
  height: 3px;
  background-color: currentColor;
}

/************************
FORMS STYLING
***********************/
.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 1em;
}
.form__group label {
  color: var(--label-color);
  margin-bottom: 0.25em;
  letter-spacing: 0.03em;
  font-size: 1.1em;
}
.form__group span {
  font-size: 0.7em;
  letter-spacing: 0.02em;
  color: #ce2727;
}
.form__group input {
  width: 100%;
  font-family: "Nunito", sans-serif;
  font-size: 1em;
  border: 1px solid var(--input-border-color);
  border-radius: 0.4em;
}
.form__submit__group {
  margin-bottom: 1em;
}
.form__submit__group input[type=submit] {
  margin-top: 1em;
  margin-bottom: 1em;
  text-transform: uppercase;
  background-image: linear-gradient(to top, var(--btn-color-1), var(--btn-color-2));
  color: var(--color-white);
  width: 100%;
  padding: 0.75em;
  font-weight: 500;
  font-family: inherit;
  border: none;
  font-size: 1.1em;
  position: relative;
  border-radius: 0.35em;
  letter-spacing: 0.05em;
  box-shadow: 0 0 4px var(--btn-shadow-color);
  transition: background-color 300ms ease-out;
}
.form__submit__group input[type=submit]:hover, .form__submit__group input[type=submit]:focus {
  cursor: pointer;
  background-image: linear-gradient(to top, var(--btn-hover-color-1), var(--btn-hover-color-2));
}
.form ::placeholder {
  font-style: italic;
}

/******************
Upload Button Styling
*********************/
.form__group input::file-selector-button {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::file-selector-button:hover, .form__group input::file-selector-button:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}

.form__group input::-ms-browse {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::-ms-browse:hover, .form__group input::-ms-browse:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}

.form__group input::-webkit-file-upload-button {
  padding: 0.7em 2.5em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  margin-right: 1em;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  color: var(--color-white);
  border: none;
  transition: background-color 300ms ease-out;
}
.form__group input::-webkit-file-upload-button:hover, .form__group input::-webkit-file-upload-button:focus {
  cursor: pointer;
  background-image: linear-gradient(60deg, #2f353b 0%, #223a53 100%);
}

/********************
Note Container
***********************/
.important__note__container {
  margin-bottom: 1.5em;
}
.important__note__container h4 {
  color: var(--warning-color);
  text-transform: uppercase;
}